<template>
  <div>
    <header class="benner-container">
      <h1 class="benner-title">我的旅程</h1>
    </header>
    <div class="content">
      <!-- 資料讀取中 -->
      <template v-if="dataLoaing">
        <div v-for="(item, index) in new Array(9)" :key="index" class="card-container">
          <MaskCard />
        </div>
      </template>
    
      <!-- 資料完成 -->
      <template v-else>
        <!-- 有資料 -->
        <template v-if="favoriteDataList.length > 0">
          <div v-for="data in favoriteDataList" :key="data.ID" class="card-container">
            <Card :data="data" :type="data.Type" :classType="'commonCard'"/>
          </div>
        </template>
        <!-- 沒資料 -->
        <template v-else>
          <NoContent/>
        </template>
      </template>
    </div>
  </div>
</template>

<script>
  import Card from '@/components/card.vue';
  import MaskCard from '@/components/mask-card.vue';
  import NoContent from '@/components/no-content.vue';
  import { mapGetters } from 'vuex';

  export default {
    computed: {
      ...mapGetters(['favorites', 'favoriteDataList', 'dataLoaing'])
    },
    methods: {
      getFavoriteDataList() {
        this.$store.dispatch("getFavoriteDataList");
      }
    },
    watch: {
      favorites() {
        this.getFavoriteDataList();
      }
    },
    created() {
      this.getFavoriteDataList();
    },
    components: {
      Card,
      NoContent,
      MaskCard
    }
  }
</script>

